<template>
  <div class="banxin cia">
    <div class="search-input">
      <input type="text" placeholder="输入id、编号查询" v-model="searchVal">
      <i class="icon" @click="filterData">查询</i>
    </div>
    <div v-if="tipTxt" class="tx-c mt10" style="color: #ccc;">{{ tipTxt }}</div>
    <div class="form-item mt10" v-if="data.length === 0 && !this.tipTxt">
      <span class="label">测试用例</span>
      <span class="value">需要含美丽伏特、帝丿释天、灬虎子|哥、美丽港资、随风 中至少一个字</span>
    </div>
    <template v-if="data.length > 0">
      <div class="article-list-foo mt10" v-for="item in data" :key="item.bh">
        <div data-v-fae5bece="" class="article-title">{{ item.id }}</div>
        <div class="form-item mt10">
          <span class="label">同盟</span>
          <span class="value">{{ item.tm }}</span>
          <span class="label">编号</span>
          <span class="value">{{ item.bh }}</span>
        </div>
        <div class="article-list pt0" v-for="(item2, j) in item.children" :key="j">
          <div class="imgFoo imgFooW border-t0">
            <img width="100%" :src="item2.img" alt="">
          </div>
          <div class="article-c">
            <div class="article-content">
              <div class="form-item border-t0">
                <span class="label">克制</span>
                <span class="value">{{ item2.kz }}</span>
              </div>
              <div class="form-item border-t0">
                <span class="label">分析</span>
                <span class="value">{{ item2.fx || '暂无' }}</span>
              </div>
              <div class="form-item border-t0">
                <span class="label">更新</span>
                <span class="value">{{ item2.upDate || '1970-01-01' }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import ciaJson from '../assets/json/cia.json';

export default {
  name: "cia",
  components: {
    
  },
  props: {},
  data() {
    return {
      data: [],
      searchVal: '美丽',
      tipTxt: ''
    }
  },
  created() {},
  mounted() {
    
  },
  methods: {
    filterData() {
      if(!this.searchVal) {
        this.tipTxt = '请输入ID、编号, 可只输入少量关键字'
        this.data = []
        return
      }
      this.tipTxt = ''
      this.data = ciaJson.filter(item => item.id.includes(this.searchVal) || item.bh.includes(this.searchVal));
    }
  },
};
</script>

